<template>
    <my-dialog v-model:show="dialogVisible">
        <post-form @create="addPost"></post-form>
    </my-dialog>
    <h1>To do List</h1>
    <div class="btn-block">
        <my-button @click="showDialog">Create post</my-button>
        <my-button @click="fetchPosts">Get server posts</my-button>
    </div>
    <div class="select-block">
        <my-select v-model="selectedSort" :options="sortOptions"></my-select>
    </div>
    <post-list v-if="!postLoading" :posts="posts" @remove="removePost"></post-list>
    <div v-else><h2>Loading...</h2></div>
</template>

<script>
    import PostList from "./components/PostList";
    import PostForm from "./components/PostForm";
    import MyDialog from "./components/UI/MyDialog.vue";
    import MyButton from "./components/UI/MyButton.vue";
    import axios from 'axios';
    import MySelect from './components/UI/MySelect.vue';

    export default {
            components: {
                PostForm,
                PostList,
                MyDialog,
                MyButton,
                MySelect,
            },
            data () {
                return {
                    posts: [],
                    dialogVisible: false,
                    postLoading: false,
                    selectedSort: '',
                    sortOptions: [
                        {value: 'title', name: 'Name'},
                        {value: 'body', name: 'Description'}
                    ]
                }
            }, 
            methods : {
                addPost (post) {
                    this.posts.push(post);
                    this.dialogVisible = false;
                },
                removePost (post) {
                    this.posts.splice(this.posts.indexOf(post), 1);
                },
                showDialog () {
                    this.dialogVisible = true;
                },
                async fetchPosts() {
                    try {
                        this.postLoading = true;
                        setTimeout(async () => {
                            const response = await axios.get('https://jsonplaceholder.typicode.com/posts?_limit=10');
                            this.posts = response.data;
                            this.postLoading = false;
                        }, 1000)
                    } catch (e) {
                        alert('ERROR');
                    }
                }
            },
            // mounted() {
            //     this.fetchPosts();
            // }
            watch: {
                selectedSort (newValue) {
                    this.posts.sort((post1, post2) => {
                        return post1[newValue]?.localeCompare(post2[newValue])
                    })
                },
            }
    }
</script>

<style lang="scss">
    #app {
        margin: 0 auto;
        padding: 0;
        box-sizing: border-box;
        max-width: 1440px;

        h1, h2 {
            text-align: center;
            font-family: 'Poppins';
            font-size: 45px;
            font-weight: 600;
        }

        h2 {
            text-align: center;
            font-size: 30px;
            font-weight: 400;
        }

        .btn-block {
            display: flex;
            justify-content: center;
            gap: 30px;
        }

        .select-block {
            text-align: center;
            padding-top: 30px;
        }

        .search-block {
            text-align: center;
            padding-top: 20px;
            
            input {
                padding: 20px;
                margin-right: 20px;
                padding: 23px 40px;
                border: 0;
                background: #fff;
                border-radius: 40px;
                font-family: "Poppins";
                font-weight: 400;
                font-size: 16px;
                border: #ec6d5e 1px solid;
            }
        }
    }
</style>